@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-family: "Montserrat", sans-serif;
}

html,
body {
    overflow-x: hidden;
}

* {
    margin: 0;
    padding: 0;
}



li {
    list-style-type: none;
    margin-top: 30px;
}

a {
    text-decoration: none;
    color: white;
}

a:hover {
    color: tomato;
    font-weight: 500;
    padding-left: 30px;
    transition: all 0.3s;
}

.menu-items {
    background-color: #011221;
    position: fixed;
    right: 0;
    top: 0;
    height: 65vh;
    width: 50%;
    border-bottom-left-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: 0.3s all;
    z-index: 2;
    opacity: 0.9;
}

.menu-switch {
    color: orangered;
    font-size: 35px;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 3;
    cursor: pointer;
    padding-right: 20px;
}

.menu-switch:hover {
    color: darkgrey;
}

.active {
    color: cyan !important;
    border: 2px solid cyan;
    padding: 10px;
    border-radius: 5px;
}

.active:hover {
    padding-left: 10px !important;
}

.footer-parent {
    margin-top: 100px;
}

.footer-content {
    background-color: #000b76 !important;
    text-align: center;
    color: #fff !important;
    font-size: 16px;
    -webkit-text-stroke: 1px yellowgreen;
    padding: 20px;
}

.social-icons {
    justify-content: center;
    width: 50%;
    padding: 20px;
    border-top: 2px dotted white;
    border-bottom: 2px dotted white;
    margin-bottom: 20px;
}

@media screen and (max-width: 600px) {
    .menu-items {
        border-bottom-left-radius: 0%;
        height: 100vh;
    }
}

.home-parent {
  height: 100vh;
}

.parts {
  height: 100%;
}

.part1 {
  height: 100%;
  width: 50%;
  background-image:  linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.part2 {
  height: 100%;
  width: 50%;
  background: linear-gradient(120deg, #e0c3fc 0%, #8ec5fc 100%);
}

.separator {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%)
}

.separator span {
  -webkit-writing-mode:vertical-lr;
          writing-mode:vertical-lr;
  -webkit-text-orientation: upright;
          text-orientation: upright;
  color: #fff;
  font-size: 60px;
  font-weight: bold;
}

@media screen and (max-width: 600px) {
.separator span {
  font-size: 30px;
}
}
.rotated-parent {
    height: 100vh;
    background: #2a2a2a;
    overflow-y: hidden;
    overflow-x: hidden;
}

.rotated-parent .row {
    border: 5px solid white;
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
}

.rotated-parent h1 {
    color: #00ff88;
    font-size: 70px;
    border-bottom: 2px dashed #ffffff;
    width: -webkit-max-content;
    width: max-content;
}

.rotated-parent p b {
    color: #ffc017;
    font-size: 24px;
}

.rotated-parent p {
    color: #ffffff;
    font-size: 24px;
}

.rotated-text-part {
    border-left: 5px solid #ffffff;
    padding-left: 25px;
    height: -webkit-max-content;
    height: max-content;
}

@media screen and (max-width: 600px) {
    .rotated-parent .row {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        border: none;
    }
    .rotated-parent h1 {
        font-size: 30px;
    }
    .rotated-text-part {
        margin-left: 20px;
    }
}

.glowing-parent {
    height: 100vh;
    background-color: #222222;
}

.glowing-text-part {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.glowing-text-part h1 {
    font-size: 70px;
    font-weight: bold;
    text-align: center;
    width: 100%;
}

.glowing-text-part p {
    font-size: 30px;
    font-weight: bold;
}

.glowing-text-part:hover {
  cursor: pointer;
    color: rgba(255, 255, 255, 0.89);
    text-shadow: 3px 3px 15px;
}

@media screen and (max-width: 600px) {
.glowing-text-part h1 {
    font-size: 30px;

}

.glowing-text-part p {
    font-size: 15px;

}
}
.market-parent {
    height: 100vh;
    background-image: linear-gradient(90deg, #f43b47 0%, #453a94 100%);
}

.market-parent h1 {
    font-size: 60px;
    font-weight: bold;
    color: white;
}

.market-parent h2 {
    font-size: 20px;
    font-weight: bold;
    color: black;
}

@media screen and (max-width: 600px) {
    .market-parent {
        min-height: 100vh;
        max-height: -webkit-max-content;
        max-height: max-content;
    }
}

.colors-container {
    height: 100vh;
    -webkit-animation: container-animation 10s ease infinite;
            animation: container-animation 10s ease infinite;
}

.letter {
    font-size: 100px;
    font-weight: bold;
    color: white;
    text-align: center;
    -webkit-text-stroke: 1px black;
}

.letter0{
  -webkit-animation : letter-anim ease 3s infinite;
          animation : letter-anim ease 3s infinite;
}
.letter1{
  -webkit-animation : letter-anim ease 2s infinite;
          animation : letter-anim ease 2s infinite;
}
.letter2{
  -webkit-animation : letter-anim ease 4s infinite;
          animation : letter-anim ease 4s infinite;
}
.letter3{
  -webkit-animation : letter-anim ease 7s infinite;
          animation : letter-anim ease 7s infinite;
}
.letter4{
  -webkit-animation : letter-anim ease 5s infinite;
          animation : letter-anim ease 5s infinite;
}
.letter5{
  -webkit-animation : letter-anim ease 6s infinite;
          animation : letter-anim ease 6s infinite;
}

@-webkit-keyframes container-animation {
    0% {
        background-image: linear-gradient(to right, #6a11cb 0%, #2575fc 100%);
        color:white
    }
    25% {
      background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
      color: red;
    }
    50% {
      background-image: linear-gradient(to top, #37ecba 0%, #72afd3 100%);
      color: blue;
    }
    75% {
      background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
      color: green;
    }
    100% {
      background-image: linear-gradient(to top, #0ba360 0%, #3cba92 100%);
      color: magenta;
    }
}

@keyframes container-animation {
    0% {
        background-image: linear-gradient(to right, #6a11cb 0%, #2575fc 100%);
        color:white
    }
    25% {
      background-image: linear-gradient(to right, #b8cbb8 0%, #b8cbb8 0%, #b465da 0%, #cf6cc9 33%, #ee609c 66%, #ee609c 100%);
      color: red;
    }
    50% {
      background-image: linear-gradient(to top, #37ecba 0%, #72afd3 100%);
      color: blue;
    }
    75% {
      background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
      color: green;
    }
    100% {
      background-image: linear-gradient(to top, #0ba360 0%, #3cba92 100%);
      color: magenta;
    }
}

@-webkit-keyframes letter-anim {
      0% {
        color:white;
        font-size: 150px;
        -webkit-transform: rotate(-25deg);
                transform: rotate(-25deg)
    }
    25% {
      color: red;
      font-size: 70px;
      -webkit-transform: rotate(-10deg);
              transform: rotate(-10deg)
    }
    50% {
      color: blue;
      font-size: 120px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg)
    }
    75% {
      color: green;
      font-size: 90px;
      -webkit-transform: rotate(10deg);
              transform: rotate(10deg);
    }
    100% {
      color: magenta;
      font-size: 110px;
      -webkit-transform: rotate(25deg);
              transform: rotate(25deg)
    }
}

@keyframes letter-anim {
      0% {
        color:white;
        font-size: 150px;
        -webkit-transform: rotate(-25deg);
                transform: rotate(-25deg)
    }
    25% {
      color: red;
      font-size: 70px;
      -webkit-transform: rotate(-10deg);
              transform: rotate(-10deg)
    }
    50% {
      color: blue;
      font-size: 120px;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg)
    }
    75% {
      color: green;
      font-size: 90px;
      -webkit-transform: rotate(10deg);
              transform: rotate(10deg);
    }
    100% {
      color: magenta;
      font-size: 110px;
      -webkit-transform: rotate(25deg);
              transform: rotate(25deg)
    }
}

@media screen and (max-width: 600px) {
    .colors-container {
        height: 100%;
    }
}

.coffeeshop-parent {
