.circle {
    height: 100%;
    width: 100%;
    background-color: #589d8d;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-clip-path: circle(40% at 95% 85%);
            clip-path: circle(40% at 95% 85%);
    z-index: 0;
}

.coffee-cup {
    z-index: 1;
}

@media screen and (max-width: 600px) {
    .coffeeshop-parent {
        height: -webkit-max-content;
        height: max-content;
    }

    .coffeeshop-text-part h2 b {
        font-size: 49px;
    }

    .circle {
        height: 0;
    }
}

